import { observable, action, computed, set, toJS } from "mobx";
import axios from "axios";
import { getRedirectPath } from "../util";
import { api } from "@/api";
import ImageCompressor from "image-compressor.js";
import * as U from "../util";

const l = console.log;
class UserStore {
  // åˆ¤æ–­ç”¨æˆ·æƒé™åŽï¼Œè·³è½¬çš„è·¯å¾„
  @observable
  redirectTo = "";

  // é”™è¯¯ä¿¡æ¯
  @observable
  errMsg = "";

  // æ˜¯å¦ç™»é™†
  @observable
  isAuth = false;

  // ç”¨æˆ·ä¿¡æ¯
  @observable
  userinfo = {};

  /**
   * ç”¨æˆ·æ³¨å†Œ
   * @param {*} param0
   */
  @action.bound
  async regisger({ user, pwd, repeatpwd, type }) {
    if (!user || !pwd || !type) {
      this.errMsg = "è¯·æˆ·åå¯†ç å¿…é¡»è¾“å…¥";
      return;
    }
    if (pwd !== repeatpwd) {
      this.errMsg = "ä¸¤æ¬¡å¯†ç è¾“å…¥ä¸ä¸€æ ·";
      return;
    }
    let r;
    try {
      r = await axios.post(api.userRegister, { user, pwd, type });
      l(r);
      if (r.code === 0) {
        // æ³¨å†ŒæˆåŠŸ
        set(this, {
          errMsg: "",
          isAuth: true,
          redirectTo: getRedirectPath({ type }),
          userinfo: r.data
        });
      } else {
        this._error(r.msg);
      }
    } catch (error) {
      this._error(String(error));
    }
  }

  /**
   * ç”¨æˆ·ç™»é™†
   */
  @action.bound
  login = ({ user, pwd }) => async e => {
    user = user.trim();
    pwd = pwd.trim();
    if (user === "" || pwd === "") {
      return (this.errMsg = "è¯·è¾“å…¥è´¦å·æˆ–è€…å¯†ç ï¼");
    }
    try {
      let r = await axios.post(api.userLogin, { user, pwd });
      // l(r)
      if (r.code === 0) {
        // ç™»é™†æˆåŠŸ
        this.errMsg = "";
        this.isAuth = true;
        this.redirectTo = getRedirectPath(r.data);
        this.userinfo = r.data;
        sessionStorage.setItem('_id', this.userinfo._id);
      } else {
        this._error(r.msg);
      }
    } catch (error) {
      this._error(String(error));
    }
  };

  @action.bound
  saveUserInfo(userinfo) {
    this.userinfo = userinfo;
  }

  /**
   * * ç”¨æˆ·å®Œå–„ä¿¡æ¯
   * @param {avatar,title,company?,money?,decs} data ä¼ä¸šå®Œå–„çš„ä¿¡æ¯èµ„æ–™
   */
  @action.bound
  update = data => async e => {
    let sd = new FormData();
    let result = await new ImageCompressor().compress(data["avatar"], {
      quality: 0.6
    });

    if (!this.userinfo._id) return (this.errMsg = "æ²¡æœ‰userid!");

    for (const k in data) {
      if (k === "avatar") {
        sd.append(k, result, this.userinfo._id + U.fileSuffix(result.name));
      } else {
        sd.append(k, data[k]);
      }
    }
    try {
      let r = await axios.post(api.update, sd);
      if (r.code === 0) {
        this.userinfo = r.data;
        this.redirectTo = getRedirectPath(r.data);
      } else {
        this.errMsg = r.msg;
      }
    } catch (error) {
      this.errMsg = String(error);
    }
  };

  _error(msg) {
    this.errMsg = msg;
    this.isAuth = false;
  }
}

export default new UserStore();
